@import "./../styles/variables.scss";


.projects{
    display:flex;
    margin:100px 100px;
    max-width:100%;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    &__subtitle{
        width: 100%;
        display: flex;
        justify-content: space-around;
        align-items: center;
        padding:0 80px;
        margin: 0 20px;
        border-bottom: 2px solid rgba(229, 229, 229);
        position: relative;
        @include mobile{
            max-width: auto;
            font-size: 1em;
            margin:2px;
            padding: 13px;
        }
        &__subTitle{
            color:rgb(194, 181, 181);

            @include mobile{
                max-width: 100%;
                height: auto;
                font-size: 6px;
                padding:0 15px;
            }
    
        }
    }
    &__title{
        color:rgb(64, 41, 41);
        padding-bottom: 3em;
      
    }

    .container{
        margin-top: 60px;
        width:100%;
        display:flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        overflow: hidden;

        @include mobile{
            width:100%;
        }

        .item{
            width:300px;
            height:200px;
            border-radius:20px;
            border:1px solid lightgray;
            margin:10px 20px;
            overflow: hidden;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            position:relative;
            transition:all 1 ease;
            cursor:pointer;

            @include mobile{
                width:330px;
                height:150px;
            }

            h3{
                position:absolute;
                font-size: 20px;
            }

            img{
                width:100%;
                height:100%;
                object-fit: cover;
                z-index:1;
            }

            &:hover{
           
                img{
                    opacity: 0.2;
                    z-index:0;
                }
            }

        }
    }
    
}